.shimmer-container {
  overflow: hidden;
}
$gemini-blue: #0062ff;
$gemini-orange: #ff9900;
.shimmer-animation {
  width: 100%;
  height: 100%;
  background: linear-gradient(-45deg, $gemini-blue 25%, $gemini-orange 50%, $gemini-blue 75%);
  background-size: 400% 400%;
  animation: shimmer 2s linear infinite;
}

@keyframes shimmer {
  0% { background-position: 0% 0%; }
  100% { background-position: 100% 100%; }
}